import axios from "axios";

const API_URL = "http://localhost:3004/signup/";
const API_URL_LOG = "http://localhost:3004/signin";
const API_URL_ITEMS = "http://localhost:3001/items";

class AuthService {
  login(email, password, firstname, lastname, location, mobileno, token) {
    return axios
      .post(API_URL_LOG, {
        email,
        password,
        firstname,
        lastname,
        location,
        mobileno,
        token,
      })
      .then((response) => {
        localStorage.setItem("user", JSON.stringify(response.data));

        return response.data;
      });
  }
  logout() {
    localStorage.removeItem("user");
    // localStorage.removeItem("cartItems");
  }
  getAllUsers() {
    return axios.get(API_URL).then((res) => res.data);
  }
  register(email, password, firstname, lastname, location, mobileno) {
    return axios.post(API_URL, {
      email,
      password,
      firstname,
      lastname,
      location,
      mobileno,
    });
  }

  deleteUser(id) {
    return axios.delete(API_URL + id).then((response) => {
      console.log(response.data);
    });
  }

  updateUser(email, password, firstname, lastname, location, mobileno, token) {
    return axios
      .put(API_URL + token, {
        email,
        password,
        firstname,
        lastname,
        location,
        mobileno,
        token,
      })
      .then((response) => {
        localStorage.setItem("user", JSON.stringify(response.data));
        return response.data;
      });
  }
  getAllItems() {
    return axios.get(API_URL_ITEMS).then((res) => res.data);
  }

  AddCart(img, title, desc, price, quantity) {
    return axios.post("http://localhost:3001/cart", {
      img,
      title,
      desc,
      price,
      quantity,
    });
  }
  ViewCart() {
    return axios.get("http://localhost:3001/cart").then((res) => res.data);
  }
  DeletefromCart(id) {
    return axios.delete("http://localhost:3001/cart/" + id);
  }

  removeAllData(items) {
    var ids = items.map((item) => item.id);
    console.log(ids);
    for (var i = 0; i < ids.length; i++) {
      axios.delete("http://localhost:3001/cart/" + ids[i]);
    }
  }
  removeSelected(ids) {
    for (var i = 0; i < ids.length; i++) {
      axios.delete("http://localhost:3001/cart/" + ids[i]);
    }
  }
  updateCart(token, img, title, desc, price, quantity) {
    return axios.put("http://localhost:3001/cart/" + token, {
      img,
      title,
      desc,
      price,
      quantity,
    });
  }

  saveView(img) {
    return axios.post("http://localhost:3001/view/", { img: img });
  }

  viewRecent() {
    return axios.get("http://localhost:3001/view/").then((res) => res.data);
  }
  removeAllRecent(items) {
    var ids = items.map((item) => item.id);
    console.log(ids);
    for (var i = 0; i < ids.length; i++) {
      axios.delete("http://localhost:3001/view/" + ids[i]);
    }
  }
}

export default new AuthService();
