import {
  ADD_TO_CART,
  REMOVE_ITEM,
  SUB_QUANTITY,
  ADD_QUANTITY,
  ADD_SHIPPING,
  GET_PRODUCTS,
  VIEWCART,
  FILTER_PRODUCTS_BY_SIZE,
  ORDER_PRODUCTS_BY_PRICE,
  FILTER_PRODUCTS_BY_CATEGORY,
  FILTER_PRODUCTS_BY_SEARCH,
  VIEWRECENT,
  RECENTFILTER,
} from "../actions/types";

const initState = {
  items: [],
  filteredItems: [],
  size: "",
  sort: "",
  cat: "",
  addedItems: [],
  total: 0,
  cartItems: [],
  recentItems: [],
  filteredrecentItems: [],
};

const cartReducer = (state = initState, action) => {
  if (action.type === GET_PRODUCTS) {
    // console.log(state);
    return {
      ...state,

      items: action.payload,
      filteredItems: action.payload,
    };
  }

  /* if (action.type === FILTER_PRODUCTS_BY_SIZE) {
    //console.log(state);
    console.log(action.payload.items);
    return {
      ...state,
      filteredItems: action.payload.items,
      size: action.payload.size,
    };
  } */

  if (action.type === FILTER_PRODUCTS_BY_CATEGORY) {
    console.log(action.payload.items);
    return {
      ...state,
      filteredItems: action.payload.items,
      cat: action.payload.cat,
    };
  }
  if (action.type === FILTER_PRODUCTS_BY_SEARCH) {
    console.log(action.payload.items);
    return {
      ...state,
      filteredItems: action.payload.items,
    };
  }
  if (action.type === ORDER_PRODUCTS_BY_PRICE) {
    console.log(state);
    return {
      ...state,
      filteredItems: action.payload.items,
      sort: action.payload.sort,
    };
  }
  if (action.type === VIEWCART) {
    var items = action.payload;

    var price = items.map((item) => item.price);
    var quantity = items.map((item) => item.quantity);
    //console.log(price);
    //console.log(quantity);
    var all = [...price, ...quantity];
    //console.log(all);
    var n = all.length / 2;
    var pl = n;
    state.total = 0;
    //console.log(n);
    for (var i = 0; i < n; i++) {
      var sum;
      sum = all[i] * all[pl];
      state.total = state.total + sum;
      pl++;
    }
    //console.log(state.total);
    return {
      ...state,
      cartItems: items,
      total: state.total,
    };
  }
  if (action.type === VIEWRECENT) {
    //console.log(action.payload);
    return {
      ...state,
      recentItems: action.payload,
      filteredrecentItems: action.payload,
    };
  }
  if (action.type === RECENTFILTER) {
    return {
      ...state,
      filteredrecentItems: action.payload,
    };
  } else {
    /* //INSIDE HOME COMPONENT
  if (action.type === ADD_TO_CART) {
    console.log(action.items);
    let addedItem = state.items.find((item) => item.id == action.id);
    //check if the action id exists in the addedItems
    let existed_item = state.addedItems.find((item) => action.id === item.id);

    if (existed_item) {
      addedItem.quantity += 1;
      return {
        ...state,
        total: state.total + addedItem.price,
      };
    } else {
      addedItem.quantity = 1;
      //calculating the total
      let newTotal = state.total + addedItem.price;

      return {
        ...state,
        addedItems: [...state.addedItems, addedItem],
        total: newTotal,
      };
    }
  }

  if (action.type === REMOVE_ITEM) {
    let itemToRemove = state.addedItems.find((item) => action.id === item.id);
    let new_items = state.addedItems.filter((item) => action.id !== item.id);

    //calculating the total
    let newTotal = state.total - itemToRemove.price * itemToRemove.quantity;
    console.log(itemToRemove);
    return {
      ...state,
      addedItems: new_items,
      total: newTotal,
    };
  }
  //INSIDE CART COMPONENT
  if (action.type === ADD_QUANTITY) {
    let addedItem = state.items.find((item) => item.id === action.id);
    addedItem.quantity += 1;
    let newTotal = state.total + addedItem.price;
    return {
      ...state,
      total: newTotal,
    };
  }
  if (action.type === SUB_QUANTITY) {
    let addedItem = state.items.find((item) => item.id === action.id);
    //if the qt == 0 then it should be removed
    if (addedItem.quantity === 1) {
      let new_items = state.addedItems.filter((item) => item.id !== action.id);
      let newTotal = state.total - addedItem.price;
      return {
        ...state,
        addedItems: new_items,
        total: newTotal,
      };
    } else {
      addedItem.quantity -= 1;
      let newTotal = state.total - addedItem.price;
      return {
        ...state,
        total: newTotal,
      };
    }
  }

  if (action.type === ADD_SHIPPING) {
    return {
      ...state,
      total: state.total + 6,
    };
  }

  if (action.type === "SUB_SHIPPING") {
    return {
      ...state,
      total: state.total - 6,
    };
  } */
    return state;
  }
};

export default cartReducer;
