export const filterCategory = (products, cat) => (dispatch) => {
  dispatch({
    type: FILTER_PRODUCTS_BY_CATEGORY,
    payload: {
      cat: cat,
      items:
        cat === ""
          ? products
          : products.filter((x) => x.category == cat.toLowerCase()),
    },
  });
};
