export const filterSearch = (products, search) => (dispatch) => {
  dispatch({
    type: FILTER_PRODUCTS_BY_SEARCH,
    payload: {
      items:
        search === ""
          ? products
          : products.filter((x) => x.subcategory == search.toLowerCase()),
    },
  });
};

export const sortProducts = (items, sort) => (dispatch) => {
  const products = items.slice();

  if (sort !== "") {
    products.sort((a, b) =>
      sort === "lowestprice"
        ? a.price > b.price
          ? 1
          : -1
        : a.price < b.price
        ? 1
        : -1
    );
  } else {
    products.sort((a, b) => (a.id > b.id ? 1 : -1));
  }

  dispatch({
    type: ORDER_PRODUCTS_BY_PRICE,
    payload: {
      sort: sort,
      items: products,
    },
  });
};
